type FeedFilter = {
  type: string;
  value: string;
};
type UpdateType = 'add' | 'remove';
type SortOption = 'featured' | 'bestseller' | 'newest' | 'priceLowToHigh' | 'priceHighToLow';

const toggleFeedFilterActive: (event: any) => void = (event) => {
  preventDefaultFunction(event);

  try {
    const target: any = event.currentTarget || event.target;
    const filterContainer: any = target.closest('.filterContainer');
    const filters: any = filterContainer.getElementsByClassName('filters')[0];
    const filterButton: any = filterContainer.getElementsByClassName('filterButton')[0];

    filterButton.classList.toggle('active');
    filters.classList.toggle('active');
  } catch (error) {
    console.log(error);
  }
};

